import { Component } from 'react';
import { AntDesign, Entypo, MaterialCommunityIcons } from '@expo/vector-icons';
import { StyleSheet, View, FlatList, Animated, TouchableOpacity, Text, Image, TextInput, Alert, ScrollView, Platform } from 'react-native';
import theme from '../assets/theme';
import entry from '../assets/entry';
import moment from 'moment';
import AsyncStorage from '@react-native-async-storage/async-storage';

export default class StartHeader extends Component {
  constructor(props) {
    super(props);
    this.state = {
    };

  }

  async componentDidMount() {
    // let session = await AsyncStorage.getItem('session');
    // this.setState({
    //   session: JSON.parse(session)
    // })
  }

  doPressLogo() {
    const { navigation, session } = this.props;
    if (session.o?.openorder) {
      navigation.navigate('Menu')
    } else {
      navigation.navigate('Start', { e: session.e, t: session.t })
    }
  }

  doLogout() {
    const { navigation, session } = this.props;

    let sessionUpdate = session;
    let service = entry[session.e]?.service;

    fetch(service + 'api/orders/' + session?.o.openorder.recKey + '/clear-order-vip-info', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json',
      },
      body: JSON.stringify({
        openorderRecKey: session?.o.openorder.recKey,
      }),
    }).then(response => response.json())
      .then(response => {
        console.log(response);
        if (response.openorder) {
          sessionUpdate.o = response;
          sessionUpdate.v = {};
          AsyncStorage.setItem('session', JSON.stringify(sessionUpdate))
          navigation.navigate('Start', { e: session.e, t: session.t })
        }
      });
  }

  render() {
    const { navigation, route, session } = this.props;
    const { } = this.state;
    console.log('openorder', session?.o?.openorder?.recKey)

    return (
      <View style={{}}>
        <View style={{ height: 40, margin: 20, marginBottom: 0, flexDirection: 'row', justifyContent: 'space-between' }}>
          <TouchableOpacity onPress={() => this.doPressLogo()}>
            <Image
              style={{ height: 40, width: 80 }}
              source={{
                uri: entry['glow']?.logo
              }}
              resizeMode='contain'
            />
          </TouchableOpacity>
          <View style={{ flex: 1, flexDirection: 'row-reverse', }}>
            <TouchableOpacity style={{ backgroundColor: theme.paid, height: 40, width: 40, borderRadius: 20, marginHorizontal: 5, alignItems: 'center', justifyContent: 'center' }}
              onPress={() => navigation.navigate('Order')}>
              <MaterialCommunityIcons name="clipboard-text" size={22} color={theme.grey9} />
            </TouchableOpacity>
            {
              session?.o?.openorder &&
              <TouchableOpacity style={{ backgroundColor: theme.reserved, height: 40, width: 40, borderRadius: 20, marginHorizontal: 5, alignItems: 'center', justifyContent: 'center' }}
                onPress={() => navigation.navigate('Cart')}>
                <MaterialCommunityIcons name="shopping" size={22} color={theme.grey9} />
              </TouchableOpacity>
            }
            {
              session?.v?.vipId ?
                <TouchableOpacity style={{ backgroundColor: theme.grey9, height: 40, width: 40, borderRadius: 20, marginHorizontal: 5, alignItems: 'center', justifyContent: 'center' }}
                  onPress={() => this.doLogout()}>
                  <MaterialCommunityIcons name="crown" size={22} color={theme.grey1} />
                </TouchableOpacity>
                :
                <TouchableOpacity style={{ backgroundColor: theme.occupied, height: 40, width: 40, borderRadius: 20, marginHorizontal: 5, alignItems: 'center', justifyContent: 'center' }}
                  onPress={() => navigation.navigate('Login')}>
                  {/* onPress={() => this.doLogout()}> */}
                  <MaterialCommunityIcons name="crown" size={22} color={theme.grey9} />
                </TouchableOpacity>
            }
          </View>
        </View>
        <View style={{ height: 24, flexDirection: 'row-reverse', marginHorizontal: 20, alignItems: 'center' }}>
          <Text style={{ width: 50, fontFamily: 'demi', fontSize: 10, textAlign: 'center', color: theme.grey1 }}>ORDER</Text>
          {
            session?.o?.openorder &&
            <Text style={{ width: 50, fontFamily: 'demi', fontSize: 10, textAlign: 'center', color: theme.grey1 }}>CART ({session?.o?.openorderItems?.length || 0})</Text>
          }
          <Text style={{ width: 50, fontFamily: 'demi', fontSize: 10, textAlign: 'center', color: theme.grey1 }}>{!session?.v?.vipId ? 'MEMBER' : 'LOG OUT'}</Text>
          <Text style={{ flex: 1, fontFamily: 'demi', fontSize: 10, textAlign: 'left', color: theme.grey1 }}>{session?.t ? `TABLE ${session.t}` : 'TAKE AWAY'}    {session?.v?.vipName?.toUpperCase() || 'GUEST'}</Text>
        </View>
      </View >
    );
  }

}

const styles = StyleSheet.create({

});
