import { Component } from 'react';
import { AntDesign, Ionicons, MaterialCommunityIcons, Entypo, MaterialIcons } from '@expo/vector-icons';
import { StyleSheet, View, TouchableOpacity, Text, TextInput, FlatList, ScrollView, Linking } from 'react-native';
import moment from 'moment';
import theme from '../assets/theme';
import entry from '../assets/entry';
import AsyncStorage from '@react-native-async-storage/async-storage';
import OrderDetail from './OrderDetail';
import StartHeader from './StartHeader';

export default class Redirect extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true,
    };
  }

  async componentDidMount() {
    const { route } = this.props;
    let session = await AsyncStorage.getItem('session');
    let masters = await AsyncStorage.getItem('masters');
    this.setState({
      session: session ? JSON.parse(session) : null,
      masters: masters ? JSON.parse(masters) : null,
    }, () => {
      this.doGet();
    })
  }

  doGet() {
    const { route, navigation } = this.props;
    const { session } = this.state;
    let service = entry[route.params.e].service;
    fetch(service + 'api/vendor/rdp/get-transaction-result?transactionId=' + route?.params?.transaction_id)
      .then(response => response.json())
      .then(response => {
        console.log(response)
        if (response.response_code === '0') {
          navigation.navigate('Result', { e: route.params.e, t: (route.params.t || ''), r: route.params.r, s: '0' })
        } else {
          navigation.navigate('Result', { e: route.params.e, t: (route.params.t || ''), r: route.params.r, s: '1' })
        }
      })
  }

  render() {
    const { navigation, route, } = this.props;
    const { loading, session, response, selectedOrder, masters, } = this.state;

    if (!loading) return (
      <View style={{ backgroundColor: 'white', }}>
      </View>
    );
  }

}

