import { Component } from 'react';
import { AntDesign, Ionicons, MaterialCommunityIcons } from '@expo/vector-icons';
import { StyleSheet, View, TouchableOpacity, Text, TextInput, Image, Alert, Linking, ScrollView, Platform, TouchableHighlightComponent } from 'react-native';
import theme from '../assets/theme';
import entry from '../assets/entry';
import AsyncStorage from '@react-native-async-storage/async-storage';
import moment from 'moment';
import StartHeader from './StartHeader';

export default class Item extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: false,
      emailReceiptFlg: 'Y',
      email: '',
    };
  }

  async componentDidMount() {
    let session = await AsyncStorage.getItem('session');
    if (session) session = JSON.parse(session);
    let masters = await AsyncStorage.getItem('masters');
    if (masters) masters = JSON.parse(masters);
    this.setState({
      session,
      masters,
      email: session.o.openorder.emailAddr,
      emailReceiptFlg: session.o.openorder.emailReceiptFlg,
    })
    const { navigation } = this.props;
    this.sub = navigation.addListener(
      'focus',
      this.didFocus.bind(this)
    );
  }

  async didFocus() {
    let session = await AsyncStorage.getItem('session');
    if (session) session = JSON.parse(session);
    this.setState({
      session: session,
    })
  }

  componentWillUnmount() {
    this.sub();
  }

  doRemove(item) {
    const { navigation, route } = this.props;
    const { masters, session, orderQty, remark, modRecKeys, selectedMenu } = this.state;
    let sessionUpdate = session;
    let service = entry[session.e]?.service;

    fetch(service + 'api/delete-openorder-item/' + session?.o.openorder.recKey, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json',
      },
      body: JSON.stringify({
        openorderItemRecKey: item.recKey,
      }),
    }).then(response => response.json())
      .then(response => {
        if (response.errMsg) {
          Alert.alert(response.errMsg)
          return;
        }
        if (response.openorder) {
          sessionUpdate.o = response;
          AsyncStorage.setItem('session', JSON.stringify(sessionUpdate))
          this.setState({
            session: sessionUpdate,
          })
        }
      });
  }

  doRedeem() {
    const { route, navigation } = this.props;
    const { masters, session, email, emailReceiptFlg } = this.state;

    let service = entry[session.e].service;

    this.setState({
      loading: true,
    }, () => {

      fetch(service + 'api/email-receipt', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json',
        },
        body: JSON.stringify({
          openorderRecKey: session.o.openorder.recKey,
          emailAddr: email,
          receiptFlg: emailReceiptFlg,
        }),
      }).then(response => response.json())
        .then(response => {
          fetch(service + 'api/create-openorder-payment', {
            method: 'POST',
            headers: {
              'Content-Type': 'application/json',
              'Accept': 'application/json',
            },
            body: JSON.stringify({
              openorderRecKey: session.o?.openorder.recKey,
              pmId: masters.paymentMethods.find(el => el.refType === 'P')?.pmId,
              amt: Math.floor(Math.min(session.o?.openorder?.balance, session.o?.openorder?.vipPtsMoney)),
              payRef: '',
            }),
          }).then(response => response.json())
            .then(response => {
              console.log(response)
              this.doGet()
            })
        })

    })
  }

  async doPay() {
    const { route } = this.props;
    const { session, email, emailReceiptFlg } = this.state;

    let webpageEntry = await Linking.getInitialURL();
    let service = entry[session.e].service;

    this.setState({
      loading: true,
    }, () => {

      fetch(service + 'api/email-receipt', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json',
        },
        body: JSON.stringify({
          openorderRecKey: session.o.openorder.recKey,
          emailAddr: email,
          receiptFlg: emailReceiptFlg,
        }),
      }).then(response => response.json())
        .then(response => {
          window.location.href = service + 'api/order/' + session.o?.openorder.recKey + '/redirect-to-payment-url'
            + '?backUrl=' + encodeURIComponent(webpageEntry.split('bistro-ext')[0] + 'bistro-ext/start?e=' + session.e + '&t=' + session.t)
            + '&redirectUrl=' + encodeURIComponent(webpageEntry.split('bistro-ext')[0] + 'bistro-ext/redirect?e=' + session.e + '&t=' + session.t + '&r=' + session.o?.openorder.recKey)
            + '&notifyUrl=' + encodeURIComponent(service + 'api/order/on-notify')
        })

    })
  }

  doGet() {
    const { navigation, route, } = this.props;
    const { session } = this.state;

    let service = entry[session?.e]?.service;
    let sessionUpdate = session;

    fetch(service + 'api/openorders/' + session?.o?.openorder?.recKey)
      .then(response => response.json())
      .then(response => {
        if (response.openorder) {
          if (response.openorder.balance === 0 && response.openorderPospays.length > 0) {
            navigation.navigate('Result', { e: session.e, t: session.t, r: response.openorder.recKey, s: '0' })
            return;
          }
          sessionUpdate.o = response;
          AsyncStorage.setItem('session', JSON.stringify(sessionUpdate))
          this.setState({
            session: sessionUpdate,
            loading: false,
          })
        }
      })
  }

  render() {
    const { navigation, route } = this.props;
    const { loading, masters, session,
      email, emailReceiptFlg } = this.state;

    console.log(session?.o)

    return (
      <View style={{ backgroundColor: 'white', minHeight: window.innerHeight, justifyContent: 'space-between' }}>
        <View>
          <StartHeader navigation={navigation} session={session} />

          <View style={{ borderBottomWidth: 1, borderBottomColor: theme.grey8, backgroundColor: 'white', }}>
            <Text style={{ fontFamily: 'demi', padding: 10, fontSize: 20, color: theme.grey1, paddingTop: 20 }}>My Cart {session?.o.openorder.recKey} <Text style={{ fontFamily: 'regular', fontSize: 16, color: theme.grey1 }}>({session?.o?.openorderItems?.length || '0'})</Text></Text>
          </View>
          <View style={{ flexDirection: 'row', padding: 10 }}>
            <Text style={{ textAlign: 'center', width: 40, fontFamily: 'demi', fontSize: 12, color: theme.grey4 }}>Qty</Text>
            <View style={{ flex: 1 }}>
              <Text style={{ fontFamily: 'demi', fontSize: 12, color: theme.grey4 }}>Item</Text>
            </View>
            <Text style={{ textAlign: 'right', width: 80, fontFamily: 'demi', fontSize: 12, color: theme.grey4 }}>S$</Text>
          </View>
          {
            session?.o?.openorderItems?.map((item, index) =>
              <View key={item.recKey.toString()} style={{ flexDirection: 'row', padding: 10 }}>
                <Text style={{ textAlign: 'center', width: 40, fontFamily: 'demi', fontSize: 18 }}>{item.orderQty}</Text>
                <View style={{ flex: 1 }}>
                  <Text style={{ fontFamily: 'demi', fontSize: 18 }}>{item.menuName}</Text>
                  {
                    item.menuMod?.length>0 &&
                    <Text style={{ fontFamily: 'regular', fontSize: 16 }}>{item.menuMod}</Text>
                  }
                  {
                    session?.o?.openorderItemMods.filter(el => el.masRecKey === item.recKey).map((el, elIndex) =>
                      <Text key={elIndex.toString()}
                        style={{ fontFamily: 'regular', fontSize: 16, color: theme.grey1 }}>- {el.name}</Text>
                    )
                  }
                  <View style={{ flexDirection: 'row' }}>
                    <TouchableOpacity style={{ marginTop: 10, width: 100 }} onPress={() => this.doRemove(item)}>
                      <Text style={{ fontFamily: 'demi', fontSize: 16, color: theme.paid }}>Remove</Text>
                    </TouchableOpacity>
                    <TouchableOpacity style={{ marginTop: 10, width: 100 }} onPress={() => navigation.navigate('Item', {
                      m: item.restmenuRecKey.toString(),
                      i: item.recKey.toString(),
                    })}>
                      <Text style={{ fontFamily: 'demi', fontSize: 16, color: theme.grey1 }}>Edit</Text>
                    </TouchableOpacity>
                  </View>
                </View>
                <Text style={{ textAlign: 'right', width: 80, fontFamily: 'demi', fontSize: 18 }}>{item.lineTotal.toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 })}</Text>
              </View>
            )
          }
          <View style={{ flexDirection: 'row', padding: 10, paddingVertical: 5, alignItems: 'baseline' }}>
            <Text style={{ fontFamily: 'demi', flex: 1, textAlign: 'right', fontSize: 14 }}>Subtotal</Text>
            <Text style={{ textAlign: 'right', width: 80, fontFamily: 'demi', fontSize: 14 }}>{session?.o?.openorder?.beforeDisc.toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 })}</Text>
          </View>
          {
            ((session?.o?.openorder?.totalDisc || 0) > 0) &&
            <View style={{ flexDirection: 'row', padding: 10, paddingVertical: 5, alignItems: 'baseline' }}>
              <Text style={{ fontFamily: 'demi', flex: 1, textAlign: 'right', fontSize: 14 }}>Discount</Text>
              <Text style={{ textAlign: 'right', width: 80, fontFamily: 'demi', fontSize: 14 }}>{session?.o?.openorder?.totalDisc.toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 })}</Text>
            </View>
          }
          <View style={{ flexDirection: 'row', padding: 10, paddingVertical: 5, alignItems: 'baseline' }}>
            <Text style={{ fontFamily: 'demi', flex: 1, textAlign: 'right', fontSize: 14 }}>Service Charge</Text>
            <Text style={{ textAlign: 'right', width: 80, fontFamily: 'demi', fontSize: 14 }}>{session?.o?.openorder?.serviceCharge.toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 })}</Text>
          </View>
          <View style={{ flexDirection: 'row', paddingHorizontal: 10, alignItems: 'baseline' }}>
            <Text style={{ fontFamily: 'demi', flex: 1, textAlign: 'right', fontSize: 18 }}>Total</Text>
            <Text style={{ textAlign: 'right', width: 80, fontFamily: 'demi', fontSize: 20 }}>{session?.o?.openorder?.grandTotal.toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 })}</Text>
          </View>
          <View style={{ flexDirection: 'row', padding: 10, paddingVertical: 5, alignItems: 'baseline' }}>
            <Text style={{ fontFamily: 'demi', flex: 1, textAlign: 'right', fontSize: 14 }}>Tax Incl.</Text>
            <Text style={{ textAlign: 'right', width: 80, fontFamily: 'demi', fontSize: 14 }}>{session?.o?.openorder?.totalTax.toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 })}</Text>
          </View>
          <View style={{ marginTop: 20 }}></View>
          {
            session?.o?.openorderPospays?.map(item =>
              <View key={item.recKey.toString()} style={{ flexDirection: 'row', paddingHorizontal: 10, alignItems: 'baseline' }}>
                <Text style={{ fontFamily: 'demi', flex: 1, textAlign: 'right', fontSize: 14 }}>{masters.paymentMethods.find(el => el.pmId === item.pmId)?.refType === 'P' ? 'Redeem' : 'Online Payment'}</Text>
                <Text style={{ textAlign: 'right', width: 80, fontFamily: 'demi', fontSize: 14 }}>{item.payMoney.toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 })}</Text>
              </View>
            )
          }
          <View style={{ marginTop: 20 }}></View>
        </View>
        {
          (session?.o?.openorder?.balance !== 0) &&
          <View>
            <TouchableOpacity style={{ marginHorizontal: 20, marginTop: 20, flexDirection: 'row', alignItems: 'center' }}
              onPress={() => this.setState({ emailReceiptFlg: emailReceiptFlg === 'Y' ? 'N' : 'Y' })}>
              {
                emailReceiptFlg === 'Y' ?
                  <AntDesign name="checksquare" size={20} color={theme.grey1} />
                  :
                  <AntDesign name="checksquareo" size={20} color={theme.grey9} />
              }
              <Text style={{ fontFamily: 'regular', marginLeft: 10, }}>Email me the receipt</Text>
            </TouchableOpacity>
            <View style={styles.searchContainer}>
              <TextInput
                style={styles.searchTextInput}
                autoCorrect={false}
                placeholder={'email'}
                placeholderTextColor={theme.grey5}
                defaultValue={email}
                onChangeText={(email) => this.setState({ email })}
              />
            </View>
            <View style={{ flexDirection: 'row', margin: 10 }}>
              {
                (Math.floor(Math.min(session?.o?.openorder?.balance, session?.o?.openorder?.vipPtsMoney)) > 0) &&
                <TouchableOpacity
                  style={{ flex: 1, height: 40, margin: 10, borderRadius: 20, backgroundColor: theme.occupied, justifyContent: 'center', alignItems: 'center' }}
                  activeOpacity={1}
                  disabled={loading}
                  onPress={() => this.doRedeem()}>
                  <Text style={{ fontFamily: 'demi', fontSize: 18, color: 'white' }}>Redeem {Math.floor(Math.min(session?.o?.openorder?.balance, session?.o?.openorder?.vipPtsMoney)).toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 })}</Text>
                </TouchableOpacity>
              }
              {
                (session?.o?.openorder?.balance > 0) &&
                <TouchableOpacity
                  style={{ flex: 1, height: 40, margin: 10, borderRadius: 20, backgroundColor: theme.paid, justifyContent: 'center', alignItems: 'center', opacity: loading ? 0.5 : 1 }}
                  activeOpacity={1}
                  disabled={loading}
                  onPress={() => this.doPay()}>
                  <Text style={{ fontFamily: 'demi', fontSize: 18, color: 'white' }}>Pay S${session?.o?.openorder?.balance?.toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 })}</Text>
                </TouchableOpacity>
              }
            </View>
          </View>
        }
      </View>
    );
  }

}

const styles = StyleSheet.create({
  searchContainer: {
    height: 50,
    borderRadius: 5,
    backgroundColor: theme.grey9,
    flexDirection: 'row',
    margin: 10,
  },
  searchIconContainer: {
    marginVertical: 10,
    marginHorizontal: 5,
    width: 40,
    alignItems: 'center',
    justifyContent: 'center',
    borderRightWidth: 1,
    borderRightColor: theme.grey7,
  },
  searchTextInput: {
    flex: 1,
    // height: 50,
    // margin: 10,
    // padding: 10,
    paddingHorizontal: 10,
    borderRadius: 5,
    // backgroundColor: 'orange',
    fontFamily: 'regular',
    fontSize: 20,
  },
  modifierTextInput: {
    fontFamily: 'varela-round',
    fontSize: 20,
    color: 'white',
    paddingHorizontal: 20,
    height: 120,
    backgroundColor: theme.grey3,
  },
  menuQtyRow: {
    height: 80,
    flexDirection: 'row',
    justifyContent: 'center',
    alignItems: 'center',
  },
  menuQtyRowFirst: {
    flex: 1,
    flexDirection: 'row',
    justifyContent: 'flex-end',
    alignItems: 'center',
    marginRight: 10,
  },
  menuQtyRowSecond: {
    flex: 1,
    justifyContent: 'center',
    alignItems: 'center',
  },
  menuQtyRowThird: {
    flex: 1,
    flexDirection: 'row',
    justifyContent: 'flex-start',
    alignItems: 'center',
    marginLeft: 10,
  },
  qtyTextInput: {
    fontFamily: 'varela-round',
    fontSize: 32,
    width: '100%',
    color: 'white',
    textAlign: 'center',
  },
  modContainer: {
    padding: 5,
    flexDirection: 'row',
    flexWrap: 'wrap',
    justifyContent: 'center',
    backgroundColor: theme.grey4,
  },
  modButton: {
    paddingHorizontal: 20,
    minHeight: 32,
    justifyContent: 'center',
    borderRadius: 16,
    backgroundColor: theme.grey2,
    margin: 5,
  },
  modText: {
    fontFamily: 'varela-round',
    color: 'white',
    fontSize: 20,
  },
});
