import { Component } from 'react';
import { AntDesign, Entypo, MaterialCommunityIcons } from '@expo/vector-icons';
import { StyleSheet, View, FlatList, Animated, TouchableOpacity, Text, Image, TextInput, Alert, ScrollView, Platform } from 'react-native';
import theme from '../assets/theme';
import moment from 'moment';
import AsyncStorage from '@react-native-async-storage/async-storage';

export default class OrderDetail extends Component {
  constructor(props) {
    super(props);
    this.state = {
    };

  }

  componentDidMount() {
  }

  render() {
    const { selectedOrder, masters } = this.props;
    const { } = this.state;

    console.log(selectedOrder, masters);

    return (
      <View>
        <View style={{ padding: 10, paddingTop: 20, flexDirection: 'row', borderBottomWidth: 1, borderBottomColor: theme.grey8, backgroundColor: 'white', alignItems: 'baseline', justifyContent: 'space-between' }}>
          <Text style={{ fontFamily: 'demi', fontSize: 20, color: theme.grey1 }}>Order #{selectedOrder.openorder.docNo}</Text>
          <Text style={{ fontFamily: 'demi', fontSize: 12, color: theme.grey1 }}>{selectedOrder.openorder.orderType === 'A' ? `TABLE ${selectedOrder.openorder.tableId}` : 'TAKE AWAY'}  {moment(selectedOrder.openorder.createDate).format('YYYY-MM-DD  HH:mm')}</Text>
        </View>
        <View style={{ marginTop: 10 }}></View>
        <View style={{ flexDirection: 'row', padding: 10 }}>
          <Text style={{ textAlign: 'center', width: 40, fontFamily: 'demi', fontSize: 12, color: theme.grey4 }}>Qty</Text>
          <View style={{ flex: 1 }}>
            <Text style={{ fontFamily: 'demi', fontSize: 12, color: theme.grey4 }}>Item</Text>
          </View>
          <Text style={{ textAlign: 'right', width: 80, fontFamily: 'demi', fontSize: 12, color: theme.grey4 }}>S$</Text>
        </View>
        {
          selectedOrder.openorderItems?.map(item =>
            <View key={item.recKey.toString()} style={{ flexDirection: 'row', padding: 10 }}>
              <Text style={{ textAlign: 'center', width: 40, fontFamily: 'demi', fontSize: 18 }}>{item.orderQty}</Text>
              <View style={{ flex: 1 }}>
                <Text style={{ fontFamily: 'demi', fontSize: 18 }}>{item.menuName}</Text>
                {
                  item.menuMod?.length > 0 &&
                  <Text style={{ fontFamily: 'regular', fontSize: 16 }}>{item.menuMod}</Text>
                }
                {
                  selectedOrder.openorderItemMods.filter(el => el.masRecKey === item.recKey).map((el, elIndex) =>
                    <Text key={elIndex.toString()}
                      style={{ fontFamily: 'regular', fontSize: 16, color: theme.grey1 }}>- {el.name}</Text>
                  )
                }
              </View>
              <Text style={{ textAlign: 'right', width: 80, fontFamily: 'demi', fontSize: 18 }}>{item.lineTotal.toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 })}</Text>
            </View>
          )
        }
        <View style={{ flexDirection: 'row', padding: 10, paddingVertical: 5, alignItems: 'baseline' }}>
          <Text style={{ fontFamily: 'demi', flex: 1, textAlign: 'right', fontSize: 14 }}>Discount</Text>
          <Text style={{ textAlign: 'right', width: 80, fontFamily: 'demi', fontSize: 14 }}>{session?.o?.openorder?.totalDisc.toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 })}</Text>
        </View>
        <View style={{ flexDirection: 'row', padding: 10, paddingVertical: 5, alignItems: 'baseline' }}>
          <Text style={{ fontFamily: 'demi', flex: 1, textAlign: 'right', fontSize: 14 }}>Service Charge</Text>
          <Text style={{ textAlign: 'right', width: 80, fontFamily: 'demi', fontSize: 14 }}>{selectedOrder?.openorder?.serviceCharge.toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 })}</Text>
        </View>
        <View style={{ flexDirection: 'row', paddingHorizontal: 10, alignItems: 'baseline' }}>
          <Text style={{ fontFamily: 'demi', flex: 1, textAlign: 'right', fontSize: 18 }}>Total</Text>
          <Text style={{ textAlign: 'right', width: 80, fontFamily: 'demi', fontSize: 20 }}>{selectedOrder?.openorder?.grandTotal.toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 })}</Text>
        </View>
        <View style={{ flexDirection: 'row', padding: 10, paddingVertical: 5, alignItems: 'baseline' }}>
          <Text style={{ fontFamily: 'demi', flex: 1, textAlign: 'right', fontSize: 14 }}>Tax Incl.</Text>
          <Text style={{ textAlign: 'right', width: 80, fontFamily: 'demi', fontSize: 14 }}>{selectedOrder?.openorder?.totalTax.toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 })}</Text>
        </View>
        <View style={{ marginTop: 20 }}></View>
        {
          selectedOrder?.openorderPospays?.map(item =>
            <View key={item.recKey.toString()} style={{ flexDirection: 'row', paddingHorizontal: 10, alignItems: 'baseline' }}>
              <Text style={{ fontFamily: 'demi', flex: 1, textAlign: 'right', fontSize: 14 }}>{masters.paymentMethods.find(el => el.pmId === item.pmId)?.refType === 'P' ? 'Redeem' : 'Online Payment'}</Text>
              <Text style={{ textAlign: 'right', width: 80, fontFamily: 'demi', fontSize: 14 }}>{item.payMoney.toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 })}</Text>
            </View>
          )
        }
        <View style={{ marginTop: 20 }}></View>
        {
          (selectedOrder?.openorder?.balance > 0) ?
            <Text style={{ fontFamily: 'regular', marginHorizontal: 20 }}>Your order will be sent once payment has been confirmed.</Text>
            :
            <Text style={{ fontFamily: 'regular', marginHorizontal: 20 }}>Payment complete. We have received your order!</Text>
        }
      </View>
    );
  }

}

const styles = StyleSheet.create({

});
