import { Component } from 'react';
import { AntDesign, Ionicons, MaterialCommunityIcons, Entypo, MaterialIcons } from '@expo/vector-icons';
import { StyleSheet, View, TouchableOpacity, Text, TextInput, FlatList, ScrollView, Linking } from 'react-native';
import moment from 'moment';
import theme from '../assets/theme';
import entry from '../assets/entry';
import AsyncStorage from '@react-native-async-storage/async-storage';
import OrderDetail from './OrderDetail';
import StartHeader from './StartHeader';

export default class Result extends Component {
  constructor(props) {
    super(props);
    this.state = {
    };
  }

  async componentDidMount() {
    const { route } = this.props;
    let session = await AsyncStorage.getItem('session');
    let masters = await AsyncStorage.getItem('masters');
    this.setState({
      session: session ? JSON.parse(session) : null,
      masters: masters ? JSON.parse(masters) : null,
    }, () => {
      route.params.s === '0' && this.doGet();
    })
  }

  doGet() {
    const { navigation, route, } = this.props;
    const { session } = this.state;

    let service = entry[session?.e]?.service;

    fetch(service + 'api/openorders/' + route.params?.r)
      .then(response => response.json())
      .then(response => {
        if (!response.openorder) return;
        // reset session.o, append session.h
        let sessionUpdate = session;
        sessionUpdate.o = {};
        if (!sessionUpdate.h.some(el => el?.openorder?.recKey?.toString() === route.params.r)) {
          sessionUpdate.h.push(response);
        }
        AsyncStorage.setItem('session', JSON.stringify(sessionUpdate));
        this.setState({
          session: sessionUpdate,
          selectedOrder: response,
        })
      })
  }

  render() {
    const { navigation, route, } = this.props;
    const { session, response, selectedOrder, masters, } = this.state;

    return (
      <View style={{ backgroundColor: 'white', minHeight: '100%' }}>
        <StartHeader navigation={navigation} session={session} />
        {
          route.params.s !== '0' &&
          <View style={{ flex: 1, backgroundColor: 'white', alignItems: 'center', justifyContent: 'center' }}>
            <AntDesign name="exclamationcircle" size={24} color={theme.reserved} />
            <Text style={{ marginTop: 10, fontFamily: 'demi', marginHorizontal: 20, textAlign: 'center' }}>Payment Unsuccessful</Text>
            <Text style={{ fontFamily: 'demi', marginHorizontal: 20, textAlign: 'center' }}>Please try again or approach our friendly staff.</Text>
          </View>
        }
        {
          (selectedOrder && route.params.s === '0') &&
          <OrderDetail selectedOrder={selectedOrder} masters={masters} />
        }
        <TouchableOpacity style={{ margin: 10, height: 60, borderRadius: 5, backgroundColor: theme.grey8, alignItems: 'center', justifyContent: 'center' }}
          onPress={() => navigation.navigate('Menu')}>
          <Text style={{ fontFamily: 'demi', fontSize: 20, letterSpacing: 2, color: theme.grey1 }}>CONTINUE</Text>
        </TouchableOpacity>
      </View>
    );
  }

}

const styles = StyleSheet.create({
  searchContainer: {
    height: 50,
    borderRadius: 5,
    backgroundColor: theme.grey9,
    flexDirection: 'row',
    margin: 10,
  },
  searchIconContainer: {
    marginVertical: 10,
    marginHorizontal: 5,
    width: 40,
    alignItems: 'center',
    justifyContent: 'center',
    borderRightWidth: 1,
    borderRightColor: theme.grey7,
  },
  searchTextInput: {
    flex: 1,
    // height: 50,
    // margin: 10,
    // padding: 10,
    paddingHorizontal: 10,
    borderRadius: 5,
    // backgroundColor: 'orange',
    fontFamily: 'regular',
    fontSize: 20,
  },
});
