import { Component } from 'react';
import { AntDesign, Ionicons, MaterialCommunityIcons } from '@expo/vector-icons';
import { StyleSheet, View, TouchableOpacity, Text, TextInput, Image, Linking, ScrollView, Platform, } from 'react-native';
import theme from '../assets/theme';
import entry from '../assets/entry';
import AsyncStorage from '@react-native-async-storage/async-storage';
import moment from 'moment';
import StartHeader from './StartHeader';
import OrderDetail from './OrderDetail';

export default class Order extends Component {
  constructor(props) {
    super(props);
    this.state = {
    };
  }

  async componentDidMount() {
    const { navigation, route } = this.props;
    let masters = await AsyncStorage.getItem('masters');
    let session = await AsyncStorage.getItem('session');
    this.setState({
      session: JSON.parse(session),
      masters: JSON.parse(masters),
    })

    this.sub = navigation.addListener(
      'focus',
      this.didFocus.bind(this)
    );
  }

  async didFocus() {
    let masters = await AsyncStorage.getItem('masters');
    let session = await AsyncStorage.getItem('session');
    this.setState({
      session: JSON.parse(session),
      masters: JSON.parse(masters),
    })
  }

  componentWillUnmount() {
    this.sub();
  }

  render() {
    const { navigation, route } = this.props;
    const { masters, session } = this.state;
    console.log('render', session?.h)
    return (
      <View style={{ backgroundColor: 'white', minHeight: window.innerHeight }}>
        <StartHeader navigation={navigation} session={session} />
        {
          session?.h?.map(el =>
            <OrderDetail key={el.openorder.recKey} selectedOrder={el} masters={masters}/>
          )
        }
      </View>
    );
  }

}

